:root {
  --main-font: "Archivo", serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body,
ul {
  margin: 0;
  padding: 0;
  color: #fff;
}

body {
  font-family: var(--main-font);
  color: #000;
}

ul {
  list-style: none;
}

a {
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.nav {
  background-color: #ffffff;
}
.nav__list {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  padding: 21.5px 70px;
  display: flex;
  justify-content: space-between;
}
.nav__logo {
  align-self: center;
}
.nav__item {
  align-self: center;
}
.nav__text {
  margin-right: 45px;
  color: #000;
}
.nav__search {
  position: relative;
}
.nav__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: hsl(0deg, 0%, 7%);
}
.nav__icon i {
  width: auto;
  height: auto;
  margin-right: 1.25em;
}
.nav .fa-sun {
  color: #ee5007;
}
.nav__searchbox {
  width: 300px;
  height: 37px;
  border: none;
  border-radius: 10px;
  padding: 0px 60px 0px 32px;
  background-color: hsl(0deg, 0%, 86%);
}
.nav__notification {
  font-size: 12px;
  position: absolute;
  background: #fff;
  padding: 5px;
  border-radius: 100%;
  right: 3px;
  top: -17px;
  color: black;
  box-shadow: 0 2px 25px 0px #000;
}
.nav .fa-magnifying-glass {
  position: absolute;
  left: 0;
  color: black;
  margin-top: 10px;
  margin-left: 9px;
}
.nav .fa-basket-shopping {
  position: absolute;
  right: 0;
  color: black;
  padding-left: 23px;
  border-left: 2px solid black;
  margin-top: 10px;
  margin-right: 15px;
}

.home {
  display: flex;
}
.home__container {
  width: 100%;
  margin-bottom: 200px;
}
.home__delivery {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 221px;
  height: 37px;
  background: linear-gradient(90deg, #f05454 51.13%, rgba(240, 84, 84, 0) 95.34%);
  border-radius: 10px;
  color: #fff;
  margin-top: 147px;
  margin-bottom: 60px;
  margin-left: 160px;
}
.home__delivery p {
  margin-right: 48px;
}
.home__typography {
  width: 45vw;
}
.home__title {
  font-size: 4rem;
  margin-bottom: 60px;
  margin-left: 160px;
}
.home__title span {
  color: hsl(0deg, 84%, 64%);
}
.home__description {
  font-size: 0.875rem;
  margin-bottom: 50px;
  margin-left: 160px;
}
.home__btn {
  padding: 0.6875em 0em;
  background-color: hsl(0deg, 84%, 64%);
  color: #fff;
  width: 110px;
  border-radius: 10px;
  border: none;
  margin-left: 160px;
}
.home__text {
  margin-top: 147px;
  text-align: center;
  margin-bottom: 20px;
}
.home__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.home__img {
  height: 100px;
  margin-bottom: 2px;
  margin-top: -35px;
  width: 130px;
}
.home__background {
  position: relative;
  width: 310px;
  height: 472px;
  background: hsl(210deg, 32%, 28%);
  border-radius: 40px;
}
.home__holder {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 57px;
  left: -108px;
  position: absolute;
  top: 20px;
}
.home__dish {
  text-align: center;
  background-color: #fff;
  border-radius: 40px;
  width: 178px;
  height: 160px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.home__product {
  font-size: 0.75rem;
  font-weight: 400;
}
.home__price {
  font-size: 0.75rem;
  color: hsl(0deg, 84%, 64%);
  margin-bottom: 10px;
  margin-top: 12px;
}
.home__cart {
  padding: 0.9375em;
  color: #fff;
  background-color: hsl(0deg, 84%, 64%);
  border-radius: 10px;
  border: none;
}

.shop {
  margin-bottom: 200px;
}
.shop__typography {
  text-align: center;
  margin-bottom: 120px;
}
.shop__title {
  font-size: 4rem;
  margin-bottom: 40px;
}
.shop__description {
  font-size: 1.5rem;
  color: hsl(0deg, 84%, 64%);
}
.shop__wrapper {
  display: flex;
}
.shop__sidebar {
  background: hsl(0deg, 84%, 64%);
  border-radius: 20px;
  width: 30vw;
  height: 1067px;
  margin-left: 30px;
}
.shop__section {
  padding: 24px;
}
.shop__category {
  font-size: 16px;
  margin-bottom: 28px;
  margin-top: 40px;
}
.shop__items {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.shop__text {
  color: hsl(0deg, 0%, 7%);
}
.shop__divider {
  height: 2px;
  background-color: #000;
  margin: 0px 20px;
}
.shop__order {
  font-size: 16px;
  margin-bottom: 28px;
}
.shop__total {
  margin-bottom: 28px;
}
.shop__btn {
  padding: 0.6875em 0em;
  background-color: hsl(0deg, 0%, 7%);
  color: #fff;
  width: 110px;
  border-radius: 10px;
  border: none;
}
.shop__grid {
  height: auto;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, auto);
}
.shop__container {
  text-align: center;
  margin-top: 20px;
  height: 290px;
}
.shop__pagination {
  text-align: center;
  grid-column-start: 1;
  grid-column-end: 5;
}
.shop__pagination li {
  margin-top: 20px;
}
.shop__pagination a {
  margin-right: 50px;
  color: hsl(0deg, 0%, 7%);
}
.shop__name {
  background-color: hsl(0deg, 84%, 64%);
  border-radius: 10px;
  margin: 20px 35px 0px;
  padding: 15px;
}
.shop__image {
  margin-bottom: 10px;
}
.shop__image img {
  width: 158px;
  height: 90px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  border-radius: 12px;
  margin-top: 20px;
}
.shop__content {
  margin-bottom: 10px;
  font-size: 0.875rem;
}
.shop__pcs {
  margin-bottom: 10px;
}
.shop__price {
  margin-bottom: 10px;
  color: hsl(0deg, 84%, 64%);
}
.shop__quantity {
  display: flex;
  gap: 14px;
  justify-content: center;
}
.shop__quantity i {
  margin-top: 4px;
}
.shop__number {
  font-size: 1.25rem;
}

.about {
  margin-bottom: 200px;
}
.about__container {
  display: flex;
  margin: 0px 70px 120px;
  gap: 86px;
}
.about__background {
  width: 218px;
  height: 336px;
  background: hsl(0deg, 84%, 64%);
  border-radius: 20px;
}
.about__img {
  margin-top: 50px;
  margin-left: 30px;
  width: 272px;
  height: 215px;
}
.about__title {
  margin-bottom: 0.75em;
  font-size: 4rem;
}
.about__description {
  line-height: 2;
}

.contact {
  background-color: hsl(0deg, 0%, 20%);
  margin-top: -4px;
}
.contact__typography {
  text-align: center;
  margin: 0px 70px;
  color: #fff;
}
.contact__title {
  font-size: 4rem;
  margin-bottom: 25px;
}
.contact__description {
  font-size: 1.25rem;
  line-height: 37px;
  margin-bottom: 135px;
}
.contact__form {
  margin: 0px 70px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
.contact__form input {
  width: 100%;
  height: 37px;
  margin-bottom: 24px;
}
.contact__textarea {
  display: flex;
  flex-direction: column;
  margin: 0px 70px;
}
.contact__textarea textarea {
  margin-bottom: 24px;
}
.contact__btn {
  width: 220px;
  padding: 0.6875em 0em;
  background-color: hsl(0deg, 84%, 64%);
  border-radius: 10px;
  border: none;
  margin-bottom: 160px;
}

.info__background {
  position: relative;
}
.info__background img {
  width: 100%;
  height: 60vw;
  max-height: 700px;
}
.info__container {
  display: flex;
  gap: 100px;
}
.info__typography {
  margin: 0px 70px;
  position: absolute;
  top: -64px;
}
.info__title {
  font-size: 4rem;
}
.info__description {
  font-size: 1.25rem;
  line-height: 37px;
  margin-bottom: 100px;
}
.info__wrapper {
  display: flex;
  flex-direction: column;
  width: 30%;
}
.info__info h3 {
  margin-bottom: 8px;
}
.info__info p {
  margin-bottom: 50px;
}
.info__icon i {
  margin-right: 20px;
  color: hsl(0deg, 0%, 7%);
  font-size: 20px;
}

.footer {
  height: 350px;
  width: 100%;
  background: rgb(41, 41, 41);
}
.footer__row {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 0em 6.25em;
}
.footer__description {
  margin-bottom: 0.3125em;
}
.footer__logo {
  width: 300px;
}
.footer__logo img {
  width: 64px;
  height: 40px;
  margin: 3.4375em 0em 1.25em 0em;
}
.footer__links {
  margin: 3.4375em 0em 1.25em 0em;
}
.footer__items {
  display: flex;
  flex-direction: column;
}
.footer__text {
  font-size: 1.5rem;
}